import React, { useState } from 'react'
import { nanoid } from 'nanoid'

let mSty = {
	position: 'fixed',
	zIndex: '1',
	paddingTop: '100px',
	left: '0',
	top: '0',
	width: '100%',
	height: '100%',
	overflow: 'auto',
	backgroundColor: 'rgba(0,0,0,0.4)',
	display: 'flex',
	flexDirection: 'column',
	border: '1px solid lawngreen',
}

export default function ModalUI (props) {
	const [show, setShow] = useState('none')
	let arrMo = ['modal','modalClose']
	const handleClose = async (e) => {
		await setShow('none')
		// console.log('e', e.target.className)
		// console.log('arrMo.indexOf(e.target.className) !== -1', arrMo.indexOf(e.target.className) !== -1)
		if (await arrMo.indexOf(e.target.className) !== -1) {
			await setShow('none')
		}
	}
	const handleShow = () => {
		console.log('handleShow')
		setShow('block')
	}
	return [
		props.btn || <button
			key={nanoid(4)}
			onClick={handleShow}
			disabled={props.disabled}
			style={{
				backgroundColor: '#1A1A1A',
				border: '1px solid #1A1A1A',
				borderRadius: '2px',
				cursor: props.disabled ? 'not-allowed' : 'pointer',
			}}>
			{props.btnTxt}
		</button>,
		
		<div key={nanoid(4)}
			style={{
				...mSty,
				display: show,
			}}
			onClick={handleClose}
			id={'myModal'} className={'modal'}
		>
			<div onClick={handleShow} className={'modal-content'} style={{
				border: '1px solid red',
				width: '40%',
				display: 'flex',
				flexDirection: 'column',
				justifyContent: 'center',
				margin: '0 auto',
				backgroundColor: '#1A1A1A',
				borderRadius: '2px',
			}}>
				<div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center', margin: '0.5vh 0.5vw', }}>
					<div onClick={handleShow} className={'modalText'}>
						{props.modalHeader}
					</div>
					<div className={'modalClose'} onClick={handleClose} style={{ fontSize: '20px' }}>
						&times;
					</div>
				</div>
				<div style={{ border: '1px solid orange', margin: '0.5vh 0.5vw' }}>
					{props.modalBody}
				</div>
			</div>
		</div>
	]
}
