import React, { useState } from 'react'
import { usePopper } from 'react-popper'
import './PopIt.css'

const PopIt = (props) => {
	const [refEl, setRefEl] = useState(null)
	const [popperEl, setPopperEl] = useState(null)
	const [arrowEl, setArrowEl] = useState(null)
	const { styles, attributes } = usePopper(refEl, popperEl, {
		modifiers: [
			{ name: 'arrow', options: { element: arrowEl } },
			{ name: 'offset', options: { offset: [0, 8], } },
		]
	})
	const [isVis, setVis] = useState(false)
	const onEnter = () => setVis(true)
	const onLeave = () => setVis(false)
	return (
		<div>
      <button type='button' ref={setRefEl} onMouseEnter={onEnter} onMouseLeave={onLeave}>
				{props.popBtn}
      </button>
			{isVis ?
				<div ref={setPopperEl} style={{ ...styles.popper, backgroundColor: '#2B2B2B', borderRadius: '2px' }} {...attributes.popper}>
					{props.popText}
					<div ref={setArrowEl} style={{ ...styles.arrow, backgroundColor: 'red' }} data-popper-arrow></div>
				</div>
				:
				null
			}
    </div>
	)
}

export default PopIt
