import React, { Component } from 'react'

class Compliance extends Component {
  constructor(props) {
    super(props)
    this.state = {
      noteTxt: ''
    }
    this.onChanged = this.onChanged.bind(this)
  }
  onChanged(e) {
    this.setState({ noteTxt: e.target.value })
  }
  render() {
    const { noteTxt } = this.state
    document.title = `Cardnado | Compliance`
    return (
      <div style={{ display: 'flex', flexDirection: 'column', textAlign: 'center', margin: '5vh 15vw' }}>
        <div style={{ fontSize: '25px', margin: '5vh 0', fontWeight: 'bold' }}>Compliance</div>
        <div style={{ margin: '1vh 0', }}>
          Maintaining financial privacy is <strong>essential</strong> to preserving our freedoms.
        </div>
        <div style={{ margin: '1vh 0', }}>
          However, it should <strong>not</strong> come at the cost of non-compliance. With Cardnado, you can always provide cryptographically verified proof of transactional history using the address you used to deposit or withdraw funds. This might be necessary to show the origin of assets held in your withdrawal address.
        </div>
        <div style={{ margin: '1vh 0', }}>
          To generate a compliance report, please enter your Cardnado Note below.
        </div>
        <form style={{ margin: '2vh 0', }}>
          <div style={{ fontWeight: 'bold' }}>Note</div>
          <input
            type={'text'}
            name={'noteInput'}
            value={noteTxt}
            onChange={this.onChanged}
            placeholder={'Please enter your note'}
            style={{
              color: '#d7dadc',
              backgroundColor: '#262626',
              border: '1px solid #262626',
              borderRadius: '2px',
              width: '98%'
            }}/>
        </form>
      </div>
    )
  }
}

export default Compliance
