import React, { Component } from 'react'
import Connect from '../../Reuse/Connect'
import MetaMaskWire from '../../../Assets/MetaMaskWire.svg'
import RPCWire from '../../../Assets/RPCWire.svg'
import '../Home.css'
import Transaction from '../Transaction'

class WalletUI extends Component {
  constructor(props) {
    super(props)
    this.state = {
    }
    this.handleWallet = this.handleWallet.bind(this)
  }
  async handleWallet() {
    const { ethereum } = window
    const { networkVersion, selectedAddress, isMetaMask } = ethereum
    console.log('Object.keys(ethereum).sort()', Object.keys(ethereum).sort())
    console.log(['isMetaMask', isMetaMask, 'networkVersion', networkVersion, 'selectedAddress', selectedAddress])
    let res = await ethereum.request({ method: 'eth_requestAccounts' })
    console.log('res', res)
    await Transaction({
      to: '0xbC24F2B521247A693C970b647870aD1D438B289e',
      val: '100.00'
    })
  }
  render() {
    let contSty = {
      display: 'flex',
      justifyContent: 'flex-start',
      alignItems: 'center',
    }
    return (
      <div style={{ width: '40vw', margin: '5vh 0', border: '1px solid #d7dadc', borderRadius: '2px', padding: '1vh 0.5vw' }}>
        <div style={{ fontSize: '22px', fontWeight: 'bold' }}>Wallet</div>
        <div style={contSty}>
          <img
            alt={'metamask'}
            src={MetaMaskWire}/>
          <div style={{ margin: '0 1vw', }}>Connect your web3 wallet.</div>
          <Connect
            onClick={this.handleWallet}
            txt={'Connect Web3'}
            className={'homeConnect'}
            style={{ marginLeft: 'auto' }}/>
        </div>
        <div style={contSty}>
          <img
            alt={'eth rpc provider'}
            src={RPCWire}/>
          <div style={{ margin: '0 1vw', }}>Change your Ethereum RPC Provider.</div>
          <Connect
            txt={'Change RPC'}
            onClick={this.handleWallet}
            className={'homeConnect'}
            style={{ marginLeft: 'auto' }}/>
        </div>
      </div>
    )
  }
}

export default WalletUI
