import React, { Component } from 'react'

class CSection extends Component {
	render () {
		let { date, info } = this.props
		return (
			<div style={{margin: '10px 20px', padding: '10px 0', borderTop: '1px solid white'}}>
				<div>{date}</div>
        <div>
          {info.map((x, i) => (
            <div key={i}>- {x}</div>
          ))}
        </div>
			</div>
		)
	}
}

export default CSection