import React, { Component } from 'react'
import web3 from '../../../web3'
import ETHTornado from '../../../ethereum/ETHTornado'
import '../Home.css'

class Withdraw extends Component {
  constructor(props) {
    super(props)
    this.state = {
      // note: '',
      // recipientAddr: '',
      note: 'tornado-eth-1-5-0xa9704ec8a041f956bcf89968472bfa775a7aada08089926b3e3a90ff7eef13493d4821192f6fc83cfdaff1d192987c9b7389e05df2b1c417f45a747a0b80',
      recipientAddr: '0x98AbD72956B4E362A03a947B55C89cc6e00A78E6',
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleDonate = this.handleDonate.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleReq = this.handleReq.bind(this)
  }
  async componentDidMount () {
  }
  
  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }
  handleDonate() {
    this.setState({ recipientAddr: process.env.REACT_APP_ETH_TORNADO_ADDRESS })
  }
  handleSubmit(e) {
    e.preventDefault()
    const { note, recipientAddr } = this.state
    if (note.length > 0 && recipientAddr.length > 0) {
      this.handleReq({ note: note, recipientAddr: recipientAddr })
    }
  }
  
  async handleReq({...args}) {
    console.log('args', args)
    const accounts = await web3.eth.getAccounts()
    console.log('accounts', accounts)
    console.log('ETHTornado.methods', ETHTornado.methods)
    // const withdraw = await ETHTornado.methods.withdraw().call()
  }
  
  render() {
    const { recipientAddr, note } = this.state
    return (
      <form
        onSubmit={this.handleSubmit}
        style={{
          display: 'flex',
          flexDirection: 'column',
          width: '25vw',
        }}>
        <div style={{ padding: '0 1vw', }}>Note</div>
        <input
          type={'text'}
          name={'note'}
          value={note}
          onChange={this.handleChange}
          style={{
            backgroundColor: '#1a1a1a',
            borderRadius: '2px',
            border: '1px solid #1a1a1a',
            width: '92%',
            margin: '0 1vw',
            alignSelf: 'center',
          }}/>
        <div style={{ display: 'flex', justifyContent: 'space-between', padding: '0 1vw', margin: '0.5vh 0' }}>
          <div>Recipient Address</div>
          <div style={{ color: '#1F33E8', cursor: 'pointer' }} onClick={this.handleDonate}>
            Donate
          </div>
        </div>
        <input
          type={'text'}
          name={'recipientAddr'}
          value={recipientAddr}
          onChange={this.handleChange}
          style={{
            backgroundColor: '#1A1A1A',
            borderRadius: '2px',
            border: '1px solid #1A1A1A',
            width: '92%',
            margin: '0 1vw',
            alignSelf: 'center',
          }}/>
        <input
          type={'submit'}
          value={'Withdraw'}
          className={'homeConnect'}
          style={{
            // width: '80px', // for mobile?
            width: '10vw',
            margin: '5vh 0',
            alignSelf: 'center',
            color: '#D7DADC',
            border: '1px solid #1F33E8',
            padding: '1vw 1vh',
            textAlign: 'center',
            borderRadius: '2px',
            backgroundColor: 'transparent'
          }}/>
      </form>
    )
  }
}

export default Withdraw
