import { Link } from 'react-router-dom'

const Connect = (props) => {
  const { to, className, onClick, txt, style } = props
  if (to === undefined) {
    return (
      <button
        className={className}
        style={{
          width: '10vw',
          fontSize: '16px',
          margin: '5vh 0',
          alignSelf: 'center',
          color: '#d7dadc',
          border: '1px solid #1F33E8',
          padding: '1vw 1vh',
          textAlign: 'center',
          borderRadius: '2px',
          backgroundColor: 'transparent',
          ...style,
        }}
        onClick={onClick}>
        {txt || 'Connect'}
      </button>
    )
  } else {
    return (
      <Link
        to={to}
        className={className}
        style={{
          width: '40%',
          alignSelf: 'center',
          color: '#d7dadc',
          textDecoration: 'none',
          border: '1px solid #1F33E8',
          padding: '1vw 1vh',
          textAlign: 'center',
          borderRadius: '2px',
          ...style,
        }}>
        Connect
      </Link>
    )
  }
}

export default Connect
