import React, { Component } from 'react'
import { nanoid } from 'nanoid'
import { formatDistance } from 'date-fns'
import './Home.css'
import ETHTornado from '../../ethereum/ETHTornado'

class Stats extends Component {
  constructor(props) {
    super(props)
    this.state = {
      usrIP: props.usrIP,
      uCount: props.uCount,
      arrTxn: [],
    }
  }
  
  async componentDidMount () {
    const events = await ETHTornado.getPastEvents('Deposit', { fromBlock: 0, toBlock: 'latest' })
    let filtered = await events.reverse().map((x, i) => {
      return {
        txnNo: parseInt(x.returnValues['1']) + 1,
        txnTime: formatDistance(parseInt(x.returnValues.timestamp + '000'), Date.now()) + ' ago',
      }
    })
    this.setState({ arrTxn: filtered, uCount: events.length })
  }
  
  render() {
    const { state: { usrIP, arrTxn, uCount }, props: { token, tokenAmt } } = this
    return (
      <div className={'statistics'}>
        <div style={{ display: 'flex' }}>
          <div style={{ fontSize: '18px', fontWeight: 'bold', padding: '0 1vw', }}>
            Statistics
          </div>
          <div style={{border: '1px solid #1F33E8', borderRadius: '2px', fontSize: '14px', margin: '0 0 0 4px', padding: '2px'}}>
            {tokenAmt} {token}
          </div>
        </div>
        <div style={{ padding: '0 1vw', fontWeight: 'bold' }}>Anonymity set</div>
        <div style={{ padding: '0 1vw' }}>{uCount} equal user deposits</div>
        <div style={{ padding: '0 1vw', fontWeight: 'bold' }}>latest deposits</div>
        <div className={'recentTxns'}>
          {arrTxn.map((x, i) => (
            <div style={{
             padding: '0 1vw', backgroundColor: `${i % 2 === 1 ? '#1a1a1a' : '#212121'}` }} key={nanoid(4)}>
              {x.txnNo}. {x.txnTime}
            </div>
          ))}
        </div>
        <div style={{ display: 'flex', justifyContent: 'space-between', padding: '0 1vw' }}>
          <div>Your IP</div>
          <div>{usrIP}</div>
        </div>
      </div>
    )
  }
}

export default Stats
