import { nanoid } from 'nanoid'
import React, { Component } from 'react'
import { DropdownButton, Dropdown } from 'react-bootstrap'
import Connect from '../../Reuse/Connect'
import RadioBtn from '../../Reuse/RadioBtn'
import web3 from '../../../web3'
import ETHTornado from '../../../ethereum/ETHTornado'
// import Verifier from '../../../ethereum/Verifier'
import { decToHex } from 'hex2dec'
import '../Home.css'

class Deposit extends Component {
  constructor(props) {
    super(props)
    this.state = {
      denomination: '0.1',
      token: 'ADA',
      connected: false,
    }
    this.handleTok = this.handleTok.bind(this)
    this.handleAmt = this.handleAmt.bind(this)
    this.handleDeposit = this.handleDeposit.bind(this)
  }
  
  async componentDidMount () {
    const accounts = await web3.eth.getAccounts()
    if (accounts.length > 0) {
      this.setState({ connected: true })
    }
  }
  
  async handleTok(e) {
    let { innerText } = e.target
    this.props.tokSelected(innerText)
    this.setState({ token: innerText })
  }
  
  async handleAmt(denomination) {
    await this.setState({ denomination: denomination })
    this.props.amtSelected(denomination)
  }
  
  async handleDeposit() {
    console.log(`%chandleDeposit()`,'color:red')
    
    const { denomination } = this.state
    console.log('denomination:', denomination)
    
    const weiValue = web3.utils.toWei(denomination, 'ether')
    console.log('weiValue', weiValue)
    
    const asBytes = decToHex(weiValue)
    console.log('asBytes', asBytes)
    
    const accounts = await web3.eth.getAccounts()
    console.log('accounts', accounts)
  
    const operator = await ETHTornado.methods.operator().call()
    console.log('operator', operator)
		
    const denom = await ETHTornado.methods.denomination().call()
    console.log('denom', denom)
    console.log(denom === weiValue)
    
  }
  
  render() {
    const { token, tokenAmt } = this.props
    
    const DropDownCoin = () => (
      <div style={{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        placeItems: 'center',
      }}>
        <div style={{ margin: '0 .5vw 0 0' }}>{token}</div>
        <div className={'ddArrow'}>
        </div>
      </div>
    )

    return [
      <div style={{ padding: '0 4px' }} key={nanoid(4)}>Token</div>,
      <DropdownButton
        key={nanoid(4)}
        id={'dd-btn'}
        title={
          <DropDownCoin/>
        }>
        <div className={'tokDropDown'}>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>ADA</Dropdown.Item>
          <Dropdown.Divider/>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>DAI</Dropdown.Item>
          <Dropdown.Divider/>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>cDAI</Dropdown.Item>
          <Dropdown.Divider/>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>USDC</Dropdown.Item>
          <Dropdown.Divider/>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>USDT</Dropdown.Item>
          <Dropdown.Divider/>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>WBTC</Dropdown.Item>
        </div>
      </DropdownButton>,
      <div style={{ padding: '0 4px' }} key={nanoid(4)}>Amount</div>,
      <div style={{ display: 'flex', justifyContent: 'space-evenly' }} key={nanoid(4)}>
        <RadioBtn amt={'0.1'} token={token} tokenAmt={tokenAmt} onClick={this.handleAmt}/>
        <RadioBtn amt={'1'} token={token} tokenAmt={tokenAmt} onClick={this.handleAmt}/>
        <RadioBtn amt={'10'} token={token} tokenAmt={tokenAmt} onClick={this.handleAmt}/>
        <RadioBtn amt={'100'} token={token} tokenAmt={tokenAmt} onClick={this.handleAmt}/>
      </div>,
      this.state.connected ?
        <Connect key={nanoid(4)} onClick={this.handleDeposit} txt={'Deposit'}/>
        :
        <Connect key={nanoid(4)} to={'/account'} className={'homeConnect'}/>
    ]
  }
}

export default Deposit
