
export default function RadioBtn(props) {
  const onClicked = () => {
    props.onClick(props.amt)
  }
  return (
    <div onClick={onClicked} style={{
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
    }}>
      <div style={{
        height: '16px',
        width: '16px',
        borderRadius: '50%',
        border: '2px solid #1F33E8',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <div style={{
          height: '12px',
          width: '12px',
          borderRadius: '50%',
          backgroundColor: props.tokenAmt === props.amt ? '#1F33E8' : '#0E0E0E'
        }}>
        </div>
      </div>
      <div>{props.amt} {props.token}</div>
    </div>
  )
}
