import React, { Component } from 'react'
import Deposit from './Deposit'
import Withdraw from './Withdraw'
import '../Home.css'

class DepWith extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isDeposit: true,
    }
    this.handleClick = this.handleClick.bind(this)
  }
  handleClick() {
    const { isDeposit } = this.state
    this.setState({ isDeposit: !isDeposit })
  }
  render() {
    const { token, tokenAmt, amtSelected, tokSelected } = this.props
    const { isDeposit } = this.state
    const DepositWithdraw = (props) => (
      <div
        className={'depWith'}
        onClick={this.handleClick.bind(this, props.txt)}
        style={{
          alignSelf: 'center',
          fontSize: '18px',
          fontWeight: 'bold',
          padding: '2px 4px',
          color: props.color ? '#1F33E8' : '#D7DADC',
         }}>
        {props.txt}
      </div>
    )
    return (
      <div className={'deposits'}>
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-between',
            borderBottom: '1px solid #d7dadc',
          }}>
          <DepositWithdraw
            color={isDeposit}
            txt={'Deposit'}/>
          <DepositWithdraw
            color={!isDeposit}
            txt={'Withdraw'}/>
        </div>
        {isDeposit ?
          <Deposit
            token={token}
            tokenAmt={tokenAmt}
            tokSelected={tokSelected}
            amtSelected={amtSelected}/>
          :
          <Withdraw/>
        }
      </div>
    )
  }
}

export default DepWith
